// .gallery { }

.gallery__list {
  display: flex;
  justify-content: center;
  gap: 15px;

  @include transition;
  transition-property: height;

  @include mq($tablet) {
    gap: 30px;
  }
}

.gallery__item {
  display: flex;
  flex-basis: calc((100% - 15px) / 2);
  flex-direction: column;
  align-items: center;
  gap: 15px;

  box-shadow: 0.3rem 0.3rem 2.1rem 0.5rem rgba($color-brand, 0.21);

  @include mq($tablet) {
    gap: 21px;

    flex-basis: calc((100% - 3 * 30px) / 4);
  }
}

.gallery__thumb {
  max-width: clamp(290px, 25vw, 300px);
}

